import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom'
import Header from './Header/Header'
import Homepage from './pages/homepage'
import Portfolio from './pages/Portfolio'
import Contact from './pages/contact'
import ProjectOne from './pages/ProjectPages/ProjectOnePage/ProjectOne'
import ProjectTwo from './pages/ProjectPages/ProjectTwoPage/ProjectTwo'
import ProjectThree from './pages/ProjectPages/ProjectThreePage/ProjectThree'
import TopScroll from './TopScroll'
import Footer from './Footer/Footer'

const App = () => {
    return (
        <Router>
            <TopScroll>
                <Header />
                <Route path='/' exact>
                    <Homepage />
                </Route>
                <Route path='/portfolio' >
                    <Portfolio />
                </Route>
                <Route path='/contact' >
                    <Contact />
                </Route>
                <Route path='/project/one' >
                    <ProjectOne />
                </Route>

                <Route path='/project/two' >
                    <ProjectTwo />
                </Route>

                <Route path='/project/three' >
                    <ProjectThree />
                </Route>

                <Footer />
            </TopScroll>
        </Router>
    );
}

export default App;