import React from 'react'
import classes from './ContactForm.module.scss'
import { useForm } from "react-hook-form"
import PrimaryButton from '../../../../components/buttons/Primary/PrimaryButton'

const ContactForm = () => {
    const { register, handleSubmit, errors } = useForm();
    const onSubmit = data => alert('Submitted');
    console.log(errors)

    return (
        <div className={classes.Contact}>
            <h1>Contact Me</h1>

            <form className={classes.ContactForm} onSubmit={handleSubmit(onSubmit)}>
                <label htmlFor="name">Name</label>
                <input name="name" placeholder='John Doe' ref={register({ required: true })} />
                {errors.name && <div className={classes.error}>This field is required</div>}

                <label htmlFor="email">Email</label>
                <input name="email" placeholder='example@example.com' ref={register({ required: true, pattern: /^([a-zA-Z0-9_\-\.]+)@([a-zA-Z0-9_\-\.]+)\.([a-zA-Z]{2,5})$/ })} />
                {errors.email && errors.email.type === 'required' && <div className={classes.error}>This field is required</div>}
                {errors.email && errors.email.type === 'pattern' && <div className={classes.error}>Please enter a valid email</div>}

                <label htmlFor="message">Message</label>
                <textarea name="message" ref={register}></textarea>


                <PrimaryButton type='submit' text='send message' />

            </form>


        </div>
    );
}

export default ContactForm;